@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
body {
  background: #5f2c82;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #49a09d, #6d4786); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-family: 'Staatliches', cursive;
}
.container{
  height: 80vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.display{
  width: 400px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;

}

.display h1{
display: block;
width: 100%;
text-align: center;
}
.drum-pad{
  width: 100px;
  height: 100px;
  font-size: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background:#cddee5;
}
.active{
  opacity: 0.8;
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
  background: #a4d6f1;
}
